import React from 'react';
import './CreateProject.css'; // Custom CSS for styling (add background image here)
import logo from '../../imgs/Logo.svg'; // Update the path to your logo
import backBtn from '../../imgs/back arrow.svg';
import { ReactComponent as LogoutIcon } from '../../imgs/Logout.svg';


const CreateProjectForm = () => {
  return (
    <div className="form-container text-white">
      <div className="header-container p-0 m-0">
        <div className="header-left p-4">
          <h1 className="header-title text-white Dashtext">Create Project</h1>
        </div>
        <div className="logo-container">
          <img src={logo} alt="Logo" className="logo" />
        </div>
        <div className="logout-icon-container ">
          <a href="/logout" className="logout-link"></a>
          <LogoutIcon className="logout-icon" />
        </div>
      </div>
      <div className="card shadow-sm formcard">
        <div className="card-body">
          <form className='form1'>
            {/* Project Theme */}
            <div className="form-group mb-3 d-flex">
              <textarea
                className="form-control"
                placeholder="Enter Project Theme"
                rows="3"
                style={{ resize: 'none' }}
              ></textarea>
            </div>

            <div className="row">
              {/* Reason */}
              <div className="col-sm-4 mb-3">
                <label>Reason</label>
                <select className="form-control">
                  <option>For Business</option>
                  <option>For Personal</option>
                  <option>For Educational</option>
                </select>
              </div>

              {/* Type */}
              <div className="col-sm-4 mb-3">
                <label>Type</label>
                <select className="form-control">
                  <option>Internal</option>
                  <option>External</option>
                </select>
              </div>

              {/* Division */}
              <div className="col-sm-4 mb-3">
                <label>Division</label>
                <select className="form-control">
                  <option>Filters</option>
                  <option>Analytics</option>
                  <option>Development</option>
                </select>
              </div>

              {/* Department */}
              <div className="col-sm-4 mb-3">
                <label>Department</label>
                <select className="form-control">
                  <option>Strategy</option>
                  <option>Marketing</option>
                  <option>Finance</option>
                </select>
              </div>

              {/* Category */}
              <div className="col-sm-4 mb-3">
                <label>Category</label>
                <select className="form-control">
                  <option>Quality A</option>
                  <option>Quality B</option>
                  <option>Quality C</option>
                </select>
              </div>

              {/* Priority */}
              <div className="col-sm-4 mb-3">
                <label>Priority</label>
                <select className="form-control">
                  <option>High</option>
                  <option>Medium</option>
                  <option>Low</option>
                </select>
              </div>

              {/* Start Date */}
              <div className="col-sm-4 mb-3">
                <label>Start Date as per Project Plan</label>
                <input
                  type="date"
                  className="form-control"
                  placeholder="D Month, Yr"
                />
              </div>

              {/* End Date */}
              <div className="col-sm-4 mb-3">
                <label>End Date as per Project Plan</label>
                <input
                  type="date"
                  className="form-control"
                  placeholder="D Month, Yr"
                />
              </div>

              {/* Location */}
              <div className="col-sm-4 mb-3">
                <label>Location</label>
                <select className="form-control">
                  <option>Pune</option>
                  <option>Mumbai</option>
                  <option>Bangalore</option>
                </select>
              </div>

              {/* Status */}
              <div className="status d-inline-flex m-0 align-middle">
                <label className="m-0 p-0">Status:</label>
                <p className="form-control-plaintext p-0"><b>Registered</b></p>
              </div>
            </div>

            {/* Save Project Button */}
            <div className="save-button-container">
              <button type="button" className="btn btn-primary rounded-5">
                Save Project
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default CreateProjectForm;
