import React, { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { CDBContainer } from 'cdbreact';
import './Chart.css'; // Import the CSS file

// Register necessary components
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const Chart = () => {
  const [data] = useState({
    labels: ['STR', 'FIN', 'QLT', 'MAN', 'STO', 'HR'],
    Percentage : [10,20,30,40,50],
    datasets: [
      {
        label: 'Total',
        backgroundColor: '#025AAB',
        borderColor: 'rgb(194, 116, 161)',
        data: [19, 7, 9, 15, 5, 10],
        borderRadius: 10, 
        
      },
      {
        label: 'Closed',
        backgroundColor: '#5AA647',
        borderColor: 'rgb(71, 225, 167)',
        data: [14, 6, 8, 15, 5, 9],
        borderRadius: 10, 
      },
    ],
  });

  useEffect(() => {
    return () => {
      // Cleanup chart instance
      ChartJS.getChart('myChart')?.destroy();
    };
  }, []);

  return (
    <CDBContainer>
      <h3 className="mt-5 text-white text-left ">Department Wise - Total Vs Closed</h3>
      <div className="chart-container mb-5"> {/* Apply custom class for width control */}
        <Bar data={data} options={{ responsive: true }} id="myChart" />
      </div>
    </CDBContainer>
  );
};

export default Chart;
