import React, { useState } from 'react';
import './ProjectTable.css'; // Custom CSS for styling
import logo from '../../imgs/Logo.svg'; // Update the path to your logo
import backBtn from '../../imgs/back arrow.svg';
import Page from '../Pagination/Page';
import { ReactComponent as LogoutIcon } from '../../imgs/Logout.svg';


const ProjectTable = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [priority, setPriority] = useState('Priority');

  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
  };

  return (
    <div className="form-container text-white">
        <div className="header-container p-0 m-0">
            <div className="header-left p-4">
                <h1 className="header-title text-white Dashtext">Project Listing</h1>
            </div>
            <div className="logo-container">
                <img src={logo} alt="Logo" className="logo" />
            </div>
            <div className="logout-icon-container ">
                <LogoutIcon className="logout-icon" />
            </div>
        </div>
     
      <div className="card m-3 shadow-sm card1 formcard">
        <div className="card-body">
          <form>
            {/* Search Bar and Sort Dropdown */}
            <div className="d-flex justify-content-between mb-3">
              <input
                type="text"
                className="form-control w-25"
                placeholder="Search..."
                value={searchTerm}
                onChange={handleSearchChange}
              />
              <div className="dropdown">
                <button
                  className="btn dropdown-toggle"
                  type="button"
                  id="sortDropdown"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Sort by : {priority}
                </button>
                <ul className="dropdown-menu" aria-labelledby="sortDropdown">
                  <li>
                    <button className="dropdown-item" onClick={() => setPriority('Recently-Viewed')}>
                      Recently Viewed
                    </button>
                  </li>
                  <li>
                    <button className="dropdown-item" onClick={() => setPriority('Status')}>
                      Status
                    </button>
                  </li>
                  <li>
                    <button className="dropdown-item" onClick={() => setPriority('Start-Date')}>
                      Start Date
                    </button>
                  </li>
                  <li>
                    <button className="dropdown-item" onClick={() => setPriority('End-Date')}>
                      End Date
                    </button>
                  </li>
                </ul>
              </div>
            </div>

            {/* Table -> Card View for Mobile */}
            <table className="table w-100 m-0 p-0">
              <thead>
                <tr style={{ backgroundColor: '#EBF5FF' }} className="table-info">
                  <th>Project Name</th>
                  <th>Reason</th>
                  <th>Type</th>
                  <th>Division</th>
                  <th>Category</th>
                  <th>Priority</th>
                  <th>Dept.</th>
                  <th>Location</th>
                  <th>Status</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td data-label="Project Name">
                    Line Filter
                    <div className="subtext">Jun-21, 2020 to Jan-01, 2021</div>
                  </td>
                  <td data-label="Reason">Business</td>
                  <td data-label="Type">Internal</td>
                  <td data-label="Division">Compressor</td>
                  <td data-label="Category">Quality A</td>
                  <td data-label="Priority">High</td>
                  <td data-label="Dept.">Strategy</td>
                  <td data-label="Location">Pune</td>
                  <td data-label="Status" className="text-secondary"><b>Running</b></td>
                  <td data-label="Action">
                    <div className="action-buttons">
                      <button type="button" className="btn btn-primary rounded-pill m-1">Start</button>
                      <button type="button" className="btn btn-outline-primary rounded-pill m-1">Close</button>
                      <button type="button" className="btn btn-outline-primary rounded-pill m-1">Cancel</button>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td data-label="Project Name">
                    Tank Filter
                    <div className="subtext">Jan-01, 2021 to Nov-20, 2021</div>
                  </td>
                  <td data-label="Reason">Business</td>
                  <td data-label="Type">External</td>
                  <td data-label="Division">Glass</td>
                  <td data-label="Category">Quality A</td>
                  <td data-label="Priority">High</td>
                  <td data-label="Dept.">Stores</td>
                  <td data-label="Location">Mumbai</td>
                  <td data-label="Status" className="text-secondary"><b>Registered</b></td>
                  <td data-label="Action">
                    <div className="action-buttons">
                      <button type="button" className="btn btn-primary rounded-pill m-1">Start</button>
                      <button type="button" className="btn btn-outline-primary rounded-pill m-1">Close</button>
                      <button type="button" className="btn btn-outline-primary rounded-pill m-1">Cancel</button>
                    </div>
                  </td>
                </tr>
                {/* Add more rows as needed */}
              </tbody>
            </table>
          </form>
        </div>
      </div>
      <Page />
    </div>
  );
};

export default ProjectTable;
